import 'lazysizes';
import 'lazysizes/plugins/bgset/ls.bgset.js';
import 'lazysizes/plugins/respimg/ls.respimg.js';
import 'lazysizes/plugins/parent-fit/ls.parent-fit.js';

import Swiper, { Navigation, Pagination, Autoplay, EffectFade, Thumbs } from "swiper";
Swiper.use([Navigation, Pagination, Autoplay, EffectFade, Thumbs]);

import MicroModal from 'micromodal';
import noUiSlider from 'nouislider';
import { Fancybox } from "@fancyapps/ui/src/Fancybox/Fancybox.js";
import Choices from "choices.js";

document.addEventListener('DOMContentLoaded', () => {

    /* full height mobile fix */
    const appHeight = () => {
        const doc = document.documentElement
        doc.style.setProperty('--app-height', `${window.innerHeight}px`)
    }
    window.addEventListener('resize', appHeight)
    appHeight();

    /* scroll check function */
    window.addEventListener('scroll', function (){
        let scroll = this.pageYOffset,
            scroll_offset = 0;

        /* header scrroll function */
        if(scroll > scroll_offset) {
            document.querySelector('.header').classList.add('scroll');
        } else {
            document.querySelector('.header').classList.remove('scroll');
        }

        /* catalog filter function */
        if(document.querySelector('.catalog-page .catalog-page__filter')){
            var elTop = document.querySelector('.catalog-page .catalog-page__filter').getBoundingClientRect().top,
                containerBottom = document.querySelector('.catalog-page').getBoundingClientRect().bottom,
                scrollTop = window.pageYOffset || document.documentElement.scrollTop,
                headerHeight = 52,
                bottomOffset = 150;
            if(window.innerWidth >= 1200){
                headerHeight = 100;
                bottomOffset = 200;
            }
            if(elTop <= headerHeight && containerBottom >= bottomOffset){
                document.querySelector('.catalog-page .catalog-page__filter').classList.add('scroll');
            }
            else{
                document.querySelector('.catalog-page .catalog-page__filter').classList.remove('scroll');
            }
        }

        /* scroll top btn */
        if(scroll >= window.innerHeight && (scroll + window.innerHeight) < document.body.clientHeight - 100){
            document.getElementById('scrlBtn').classList.add('show');
        }
        else{
            document.getElementById('scrlBtn').classList.remove('show');
        }
    });

    /* main menu function */
    document.getElementById('hamburger').addEventListener('click', function (){
        document.querySelector('body').classList.toggle('overflow');
        this.classList.toggle('is-active');
        document.querySelector(".hmain-menu").classList.toggle('show');
    });
    /* fix all selects */
    document.querySelectorAll('select.choice-select').forEach(s=>{
        const customSelects = new Choices(s, {
            searchEnabled: false,
            itemSelectText: ''
        });
    });
    /* fix unwrap slider items form complete-slider */
    if(window.innerWidth < 1200){
        document.querySelectorAll('.complete-list__item').forEach(el => {
            var curEl = el.querySelector('.descr-block .info-block');
            var parent = curEl.parentNode;
            while (curEl.firstChild) parent.insertBefore(curEl.firstChild, curEl);
            parent.removeChild(curEl);
        });
    }
    /* main menu categories function */
    document.querySelectorAll('.hmain-menu__categories .categories-list__item').forEach(n=>{
        n.querySelector('.categories-list__link').addEventListener('click', function (){
            document.querySelector('.categories-list__item.active').classList.remove('active');
            this.parentElement.classList.add('active');
            document.querySelector('.subcategories-list.show').classList.remove('show');
            document.querySelector('.subcategories-list[data-category="'+this.getAttribute('data-category')+'"]').classList.add('show');
        });
    });
    /* main menu sublist functions */
    document.querySelectorAll('.subcategories-list__item.submenu').forEach(n=>{
        n.querySelector('.subcategories-list__link').addEventListener('click', function (){
            this.classList.toggle('active');
            this.nextElementSibling.classList.toggle('show');
        })
    })

    /* search open function */
    document.getElementById('search-open').addEventListener('click', function (){
        document.querySelector('body').classList.toggle('overflow');
        document.getElementById('search-result').classList.add('show');
        if(window.innerWidth >= 1200){
            document.querySelector('.header__main .search-block').classList.add('open');
            document.querySelector('.header__main .phone-link').classList.add('open-search');
        }
    });
    document.getElementById('search-close').addEventListener('click', function (){
        document.querySelector('body').classList.toggle('overflow');
        document.getElementById('search-result').classList.remove('show');
    });

    /* catalog filter open function */
    if(document.querySelector('.catalog-page__filter .filter-link')){
        document.querySelector('.catalog-page__filter .filter-link').addEventListener('click', function (){
            document.querySelector('body').classList.add('overflow');
            document.getElementById('filter-block').classList.add('show');
        });
        document.getElementById('filter-close-btn').addEventListener('click', function (){
            document.querySelector('body').classList.remove('overflow');
            document.getElementById('filter-block').classList.remove('show');
        });
        /* filter section */
        document.querySelectorAll('.filter-form__section').forEach(section => {
            section.querySelector('.section-name').addEventListener('click', function (){
                this.parentElement.classList.toggle('active');
            });
        });
        const   price_range = document.getElementById('price-slider'),
                price_from = document.getElementById('price_from'),
                price_from_value = document.getElementById('price_from__value'),
                price_to = document.getElementById('price_to'),
                price_to_value = document.getElementById('price_to__value'),
                price_min = price_range ? parseInt(price_range.getAttribute('data-min')) : '',
                price_max = price_range ? parseInt(price_range.getAttribute('data-max')) : '';
        if(price_range){
            noUiSlider.create(price_range, {
                start: [price_min, price_max],
                connect: true,
                range: {
                    'min': price_min,
                    'max': price_max
                }
            });
            price_range.noUiSlider.on('update', function (values, handle) {

                var value = values[handle];

                if (handle) {
                    price_to.value = Math.round(value);
                    price_to_value.innerHTML = Math.round(value).toLocaleString() + ' ₽';
                } else {
                    price_from.value = Math.round(value);
                    price_from_value.innerHTML = Math.round(value).toLocaleString() + ' ₽';
                }
            });
            price_from.addEventListener('change', function () {
                price_range.noUiSlider.set([this.value, null]);
            });
            price_to.addEventListener('change', function () {
                price_range.noUiSlider.set([null, this.value]);
            });
        }

    }

    /* profile menu function */
    document.getElementById('profile-link').addEventListener('click', function (){
        document.getElementById('profile-menu').classList.toggle('show');
    });

    /* cart menu function */
    document.getElementById('minicart-link').addEventListener('click', function (){
        document.querySelector('body').classList.toggle('overflow');
        document.getElementById('minicart').classList.toggle('show');
    });
    document.getElementById('minicart-close').addEventListener('click', function (){
        document.querySelector('body').classList.remove('overflow');
        document.getElementById('minicart').classList.remove('show');
    });

    /* cookie close function */
    document.getElementById('cookie-close').addEventListener('click', function (){
        document.getElementById('cookie-modal').classList.remove('show');
    });

    /* check and close function */
    document.onclick = function (e){
        var hamburger = document.getElementById('hamburger'),
            menuContainer = document.querySelector('.hmain-menu'),
            profileBtn = document.getElementById('profile-link'),
            profileContainer = document.querySelector('.profile-menu'),
            searchBtn = document.getElementById('search-open'),
            searchContainer = document.getElementById('search-result'),
            cartBtn = document.getElementById('minicart-link'),
            cartContainer = document.getElementById('minicart'),

            filterBtn = document.querySelector('.catalog-page__filter .filter-link'),
            filterContainer = document.getElementById('filter-block');
        /* profile / faq short menu */
        var faq_menu = document.querySelector('nav.faq-menu'),
            profile_menu = document.querySelector('nav.profile-menu');
        /* menu check */
        if(menuContainer.classList.contains('show') && !menuContainer.contains(e.target) && !hamburger.contains(e.target)){
            menuContainer.classList.remove('show');
            hamburger.classList.toggle('is-active');
            document.querySelector('body').classList.toggle('overflow');
        }
        /* profile check */
        if(profileContainer.classList.contains('show') && !profileContainer.contains(e.target) && !profileBtn.contains(e.target)){
            profileContainer.classList.remove('show');
        }
        /* search check */
        if(searchContainer.classList.contains('show') && !searchContainer.contains(e.target) && !searchBtn.contains(e.target)){
            searchContainer.classList.remove('show');
            document.querySelector('body').classList.toggle('overflow');
            if(window.innerWidth >= 1200){
                document.querySelector('.header__main .search-block').classList.remove('open');
                document.querySelector('.header__main .phone-link').classList.remove('open-search');
            }
        }
        /* cart check */
        if(cartContainer.classList.contains('show') && !cartContainer.contains(e.target) && !cartBtn.contains(e.target)){
            cartContainer.classList.remove('show');
            document.querySelector('body').classList.toggle('overflow');
        }
        /* profile menu */
        if(profile_menu && profile_menu.classList.contains('show') && !profile_menu.contains(e.target)){
            profile_menu.classList.remove('show');
        }
        /* faq menu  */
        if(faq_menu && faq_menu.classList.contains('show') && !faq_menu.contains(e.target)){
            faq_menu.classList.remove('show');
        }
        /* filter check */
        /* cart check */
        if(filterContainer && filterContainer.classList.contains('show') && !filterContainer.contains(e.target) && !filterBtn.contains(e.target)){
            filterContainer.classList.remove('show');
            document.querySelector('body').classList.toggle('overflow');
        }

    }

    /* main page stocks slider */
    const mainSlider = new Swiper('#main-slider', {
        preloadImages: false,
        lazy: true,
        speed: 800,
        slidesPerView: 1,
        spaceBetween: 0,
        loop: true,
        autoplay: {
            delay: 5000,
        },
        pagination: {
            el: "#main-slider .swiper-pagination",
            clickable: true
        }
    });

    /* catalog section slider */
    document.querySelectorAll('.catalog-section').forEach(n=>{
        const catalogSlider = new Swiper(n.querySelector('.catalog-slider'), {
            preloadImages: false,
            lazy: true,
            speed: 800,
            slidesPerView: "auto",
            spaceBetween: 16,
            pagination: {
                el: n.querySelector('.swiper-pagination'),
                clickable: true
            },
            navigation: {
                nextEl: n.querySelector('.btn-next'),
                prevEl: n.querySelector('.btn-prev'),
            },
            breakpoints: {
                768: {
                    spaceBetween: 24,
                    centeredSlides: true,
                    loop: true,
                    loopAdditionalSlides: 5
                }
            },
        });
    });
    /* catalog colors item slider */
    document.querySelectorAll('.colors-slider').forEach(n=>{
        let colorsArray = [];
        n.querySelectorAll('.product-link').forEach(l => {
           colorsArray.push(l.getAttribute('data-color'));
        });
        const colorsSlider = new Swiper(n, {
            preloadImages: false,
            lazy: true,
            speed: 800,
            slidesPerView: "auto",
            spaceBetween: 16,
            pagination: {
                el: n.closest('.slide-item').querySelector('.colors-pagination'),
                clickable: true,
                renderBullet: function (index, className) {
                    return '<span class="product-colors__item ' + className + '" style="background-color: '+ colorsArray[index] +';"></span>';
                },
            },
            navigation: {
                nextEl: n.querySelector('.color-next'),
                prevEl: n.querySelector('.color-prev'),
            }
        });
    });
    /* catalog-page catalog colors slider */
    document.querySelectorAll('.colors-slider-catalog').forEach(n=>{
        let colorsArray = [];
        n.querySelectorAll('.product-link').forEach(l => {
            colorsArray.push(l.getAttribute('data-color'));
        });
        const colorsMSlider = new Swiper(n, {
            preloadImages: false,
            lazy: true,
            speed: 800,
            allowTouchMove: false,
            slidesPerView: "auto",
            spaceBetween: 15,
            pagination: {
                el: n.closest('.product-item').querySelector('.colors-pagination'),
                clickable: true,
                renderBullet: function (index, className) {
                    return '<span class="product-colors__item ' + className + '" style="background-color: '+ colorsArray[index] +';"></span>';
                },
            },
            navigation: {
                nextEl: n.querySelector('.color-next'),
                prevEl: n.querySelector('.color-prev'),
            }
        });
    });
    if(window.innerWidth < 1200){
        /*  product page main slider */
        const productSlider = new Swiper('#product-slider', {
            autoHeight: false,
            preloadImages: false,
            lazy: true,
            speed: 800,
            slidesPerView: 1,
            spaceBetween: 0,
            pagination: {
                el: "#product-slider .swiper-pagination",
                clickable: true
            }
        });
    }
    /* video link function */
    document.querySelectorAll('.product-slider .video-link').forEach(link=> {
        link.addEventListener('click', function (){
           let video = link.querySelector('.video-item');
           if(link.classList.contains('active')){
               video.pause();
               link.classList.remove('active');
           }
           else{
               video.play();
               link.classList.add('active');
           }

        });
    })
    /* product page color slider */
    const productColorSlider = new Swiper('#product-colors', {
        lazy: true,
        speed: 800,
        slidesPerView: 'auto',
        spaceBetween: 0,
        loop: false,
        breakpoints: {
            1199: {
                slidesPerView: 5,
                spaceBetween: 16,
                navigation: {
                    nextEl: '#product-colors .swiper-navigation .nav-btn.btn-next',
                    prevEl: '#product-colors .swiper-navigation .nav-btn.btn-prev',
                },
            }
        },

    });
    /* product page complete slider */
    if(window.innerWidth < 1200){
    const completeSlider = new Swiper('#complete-slider', {
        modules: [Pagination, Navigation],
        preloadImages: false,
        lazy: true,
        speed: 800,
        centeredSlides: false,
        slidesPerView: "auto",
        spaceBetween: 16,
        pagination: {
            el: '#complete-slider .swiper-pagination',
            clickable: true
        },
        navigation: {
            nextEl: '#complete-slider .swiper-navigation .nav-btn.btn-next',
            prevEl: '#complete-slider .swiper-navigation .nav-btn.btn-prev',
        },
        breakpoints: {
            769: {
                spaceBetween: 24,
                centeredSlides: false,
                loop: true,
                loopAdditionalSlides: 5
            }
        },
    });
    }
    /* product page gallery */
    /* about-page sertificate gallery*/
    if(window.innerWidth >= 1200){
        Fancybox.bind('#product-slider a[data-link="gallery"]', {
            groupAll: true,
            buttons: [
                "slideShow",
                "zoom",
                "fullScreen",
                "close"
            ],
            loop: true,
            protect: true,
            infinite: true
        });
    }
    else{
        Fancybox.bind('#product-slider a[data-link="gallery"]', {
            groupAll: true,
            buttons: [],
            loop: true,
            protect: true,
            infinite: true
        });
    }
    /* product page tabs function */
    document.querySelectorAll('.product-description .descr-tab').forEach(tab => {
        tab.querySelector('.tab-title').addEventListener('click', function (){
            this.parentElement.classList.toggle('active');
        })
    })
    /* count group function */
    document.querySelectorAll('.count-group').forEach(n=>{
        n.querySelectorAll('.count-btn').forEach(n => {
            n.addEventListener('click', function (){
                var curBlock = this.closest('.count-group'),
                    curInput = curBlock.querySelector('.count-input'),
                    curValue = parseInt(curInput.value);
                if(this.classList.contains('minus-btn')){
                    if(curValue <= 1){
                        curValue = 1;
                    }
                    else{
                        curValue = curValue - 1;
                    }
                }
                else{
                    curValue = curValue + 1;
                }
                curInput.value = curValue;
            });
        })
    });

    /* minicart slider function */
    const minicartSlider = new Swiper(document.getElementById('minicart-slider'), {
            preloadImages: false,
            lazy: true,
            speed: 800,
            slidesPerView: "auto",
            spaceBetween: 16,
            pagination: {
                el: document.querySelector('#minicart-slider .swiper-pagination'),
                clickable: true
            },
            navigation: {
                nextEl: document.querySelector('#minicart-slider .btn-next'),
                prevEl: document.querySelector('#minicart-slider .btn-prev'),
            },
        });

    /* footer menu function */
    if(window.innerWidth < 1200){
        document.querySelectorAll('.footer__menu__column').forEach(n=>{
            n.querySelector('.column-title').addEventListener('click', function (){
                if(this.classList.contains('active')){
                    this.classList.remove('active');
                    this.nextElementSibling.classList.remove('show');
                }
                else{
                    this.classList.add('active');
                    this.nextElementSibling.classList.add('show');
                }
            })
        })
    }

    /* scroll btn function */
    document.getElementById('scrlBtn').addEventListener('click', function (){
        window.scrollTo({
            top: 0,
            behavior: "smooth"
        });
    });

    /* about page slider */
    if(window.innerWidth <= 768){
        const aboutSlider = new Swiper('#full-slider', {
        modules: [Pagination, Navigation],
        lazy: true,
        speed: 800,
        slidesPerView: 'auto',
        spaceBetween: 16,
        navigation: {
            prevEl: '.swiper-navigation .nav-btn.btn-prev',
            nextEl: '.swiper-navigation .nav-btn.btn-next',
        },
        pagination: {
            el: '#full-slider .swiper-pagination',
            type: 'fraction'
        }
    });
    }
    /* about page shops info */
    if(document.getElementById('city-info_link')){
        document.getElementById('city-info_link').addEventListener('click', function (){
            document.querySelector('body').classList.add('overflow');
            document.getElementById('city-info').classList.add('show');
        });
        document.getElementById('city-info_close').addEventListener('click', function (){
            document.querySelector('body').classList.remove('overflow');
            document.getElementById('city-info').classList.remove('show');
        });
        document.querySelectorAll('.shops-info__content .city-info .city-info__item').forEach(item => {
            item.querySelector('.place-name').addEventListener('click', function (){
                this.parentElement.classList.toggle('show');
            });
        })
    }
    /* articles page blog slider */
    if(window.innerWidth < 768){
        const ablogSlider = new Swiper('#articles-slider', {
        modules: [Pagination, Navigation],
        lazy: true,
        speed: 800,
        slidesPerView: 'auto',
        spaceBetween: 16,
        navigation: {
            prevEl: '.swiper-navigation .nav-btn.btn-prev',
            nextEl: '.swiper-navigation .nav-btn.btn-next',
        }
    });
    }
    /* article look photos */
    Fancybox.bind('.article-page__lookbook a[data-link="gallery"]', {
        groupAll: true,
        buttons: [
            "slideShow",
            "zoom",
            "fullScreen",
            "close"
        ],
        loop: true,
        protect: true,
        infinite: true
    });
    /* blog page menu */
    if(document.querySelector('.blog-menu__list .blog-menu__item.active .blog-menu__link')){
        document.querySelector('.blog-menu__list .blog-menu__item.active .blog-menu__link').addEventListener('click', function (e){
            e.preventDefault();
            document.querySelector('.about-page__content .blog-menu').classList.toggle('show');
        });
    }
    /* faq page menu */
    // faq page menu
    if(document.querySelector('.faq-menu__list .faq-menu__item.active .faq-menu__link')){
        document.querySelector('.faq-menu__list .faq-menu__item.active .faq-menu__link').addEventListener('click', function (e){
            e.preventDefault();
            document.querySelector('.faq-page__content .faq-menu').classList.toggle('show');
        });
    }
    // faq page tabs
    document.querySelectorAll('.faq-page__tab').forEach(tab => {
        tab.querySelector('.tab-title').addEventListener('click', function (){
            this.parentElement.classList.toggle('active');
        })
    });

    /* auth page */
    // tabs
    document.querySelectorAll('.auth-form__content__links .tab-link').forEach(link => {
       link.addEventListener('click', function (){
           const curTab = link.getAttribute('data-type');
           document.querySelectorAll('.auth-form__content__links .tab-link').forEach(lnk => {
               lnk.classList.remove('active');
           });
           document.querySelectorAll('.auth-form__content__tabs .tab-item').forEach(tab => {
               tab.classList.remove('active');
               tab.style.display = 'none';
           });
           document.querySelector('.auth-form__content__links .tab-link[data-type="'+ curTab +'"]').classList.add('active');
           document.querySelector('.auth-form__content__tabs .tab-item[data-type="'+ curTab +'"]').classList.add('active');
           document.querySelector('.auth-form__content__tabs .tab-item[data-type="'+ curTab +'"]').style.display = 'block';
       })
    });
    // show pass
    if(document.querySelector('input[name="show_pass"]')){
        document.querySelector('input[name="show_pass"]').addEventListener('click', function (){
        const pass_input = document.querySelector('.email-form input[name="pass"]');
        if (pass_input.type === "password") {
            pass_input.type = "text";
        } else {
            pass_input.type = "password";
        }
    });
    }
    // revover form
    if(document.querySelector('.auth-page')){
        document.querySelectorAll('.auth-page .recover-link').forEach(lnk => {
            lnk.addEventListener('click', function (){
                document.querySelectorAll('.auth-form__content__tabs .tab-item').forEach(tab => {
                    tab.classList.remove('active');
                    tab.style.display = 'none';
                });
                document.querySelector('.auth-form__content__tabs .tab-item[data-type="recover"]').classList.add('active');
                document.querySelector('.auth-form__content__tabs .tab-item[data-type="recover"]').style.display = 'block';
            });
        })
    }
    /* recently slider */
    document.querySelectorAll('.recently-slider').forEach(rs => {
        const recentlySlider = new Swiper('.recently-slider', {
            modules: [Pagination, Navigation],
            preloadImages: false,
            lazy: true,
            speed: 800,
            centeredSlides: false,
            slidesPerView: "auto",
            spaceBetween: 16,
            pagination: {
                el: rs.parentElement.querySelector('.swiper-pagination'),
                clickable: true
            },
            navigation: {
                nextEl: rs.querySelector('.swiper-navigation .nav-btn.btn-next'),
                prevEl: rs.querySelector('.swiper-navigation .nav-btn.btn-prev'),
            },
            breakpoints: {
                769: {
                    spaceBetween: 24,
                    centeredSlides: false,
                    loop: true,
                    loopAdditionalSlides: 5
                }
            },
        });
    });

    /* profile page */
    // show profile info
    document.querySelectorAll('.profile-info-link').forEach(lnk => {
        lnk.addEventListener('click', function (){
            document.querySelector('.profile-page .profile-page__profile-info').classList.toggle('active');
        });
    })
    // profile page menu
    if(document.querySelector('.profile-menu__list .profile-menu__item.active .profile-menu__link')){
        document.querySelector('.profile-menu__list .profile-menu__item.active .profile-menu__link').addEventListener('click', function (e){
            e.preventDefault();
            document.querySelector('.profile-page__content .profile-menu').classList.toggle('show');
        });
    }
    // profile page tabs
    document.querySelectorAll('.profile-page .profile-tab').forEach(tab => {
        tab.querySelector('.tab-title').addEventListener('click', function (){
            this.parentElement.classList.toggle('active');
        })
    });
    // profile page orders show
    document.querySelectorAll('.profile-page .orders-list .orders-list__item .order-number').forEach(el => {
        el.addEventListener('click', function (){
           el.parentElement.parentElement.classList.toggle('active');
        });
    })
    /* modal */
    MicroModal.init({
        onShow: modal => document.querySelector('body').classList.add('overflow'),
        onClose: modal => checkMenu(),
        disableFocus: true,
        awaitOpenAnimation: true,
        awaitCloseAnimation: true
    });
    function checkMenu(){
        /* remove body overflow if menu is close */
        if(!document.querySelector('.hmain-menu').classList.contains('show')){
            document.querySelector('body').classList.remove('overflow')
        }
    }


    /* test modal */
    // MicroModal.show('modal-cities');
    // MicroModal.show('modal-changepass');
    // MicroModal.show('modal-product');
    // MicroModal.show('modal-delete');
    // MicroModal.show('modal-sizes');
    // MicroModal.show('modal-remember');
    /* test cookie */
    // document.getElementById('cookie-modal').classList.add('show');
});